<template>
  <div id="home">
    <Navigation></Navigation>
    <div class="form-list" v-if="onProjectList.length !== 0 || endProjectList.length !== 0">
      <div class="list-header flex-row">
        <span class="w320">进行中的项目</span>
        <span class="w200">上次打开的时间</span>
        <span class="w160">已提交的病例数</span>
        <span class="w160">已保存的病例数</span>
        <span class="w128">项目管理员</span>
      </div>
      <div class="lists">
        <div class="list flex-row" v-for="(item, index) in onProjectList">
          <router-link :to="'/project/'+item.projectid+'/workdata'" class="flex-row w320">
            <img src="../assets/icon_svg/icon_project_ing.svg" style="margin-right: 8px;">
            <span class="name">{{ item.u_projectName }}waefv为钱放白醋是不VS的v</span>
          </router-link>
          <span class="w200">{{ item.opentime.substr(0,10) }}</span>
          <span class="w160">{{item.commitnum}}</span>
          <span class="w160">{{item.savenum}}</span>
          <span class="w128">{{item.adminname}}</span>
        </div>
      </div>

      <div class="list-header flex-row" style="margin-top: 50px;">
        <span class="w320">已结束的项目</span>
        <span class="w200">上次打开的时间</span>
        <span class="w160">已提交的病例数</span>
        <span class="w160">已保存的病例数</span>
        <span class="w128">项目管理员</span>
      </div>
      <div class="lists">
        <div class="list flex-row" v-for="(item, index) in endProjectList">
          <span class="w320 overflow">
            <img src="../assets/icon_svg/icon_project_end.svg">
            <span>{{ item.u_projectName }}</span>
          </span>
          <span class="w200">{{ item.opentime.substr(0,10) }}</span>
          <span class="w160">{{item.commitnum}}</span>
          <span class="w160">{{item.savenum}}</span>
          <span class="w128">{{item.adminname}}</span>
        </div>
      </div>

    </div>

    <div class="empty" v-if="empty" style="margin-top: 160px;">
      <img src="../assets/empty/home_empty.svg" alt="medicayun">
    </div> 
  </div>
</template>
<script>
  import Navigation from '../components/navigation.vue'
  export default {
    name: 'Home',
    data () {
      return {
        endProjectList: [],
        onProjectList: [],
        empty: false
      }
    },
    components: {
      Navigation
    },
    created () {
      this.getProjectList()
    },
    methods: {
      getProjectList () {
        this.$http.GetProjects({userid: this.$root.userid}).then((response) => {
          if (!response.length) {
            this.empty = true
            return
          }
          for (let i in response) {
            response[i].type === 1 ? this.onProjectList.push(response[i]) : this.endProjectList.push(response[i])
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    }
  }
</script>
<style scoped>
.form-list {
  width: 1000px;
  margin: 96px auto 20px;
}

.list-header {
  font-weight: bold;
  font-size: 16px;
  color: rgba(0,0,0,.54); 
  background: #fff;
  border: none;
  line-height: 48px;
  padding: 0 16px;
}

.list {
  justify-content: space-between;
  align-items: center;
  background: #fff;
  font-size: 16px;
  height: 56px;
  color: rgba(0, 0, 0, .54);
  border-top: 1px solid rgba(0, 0, 0, .12);
  padding: 0 16px;
}
.list:hover {
  background: rgba(70,141,241,.12);
}
.list .name {
  color: rgba(0, 0, 0, .87);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  flex: 1;
  margin-right: 20px;
}

</style>



